'use client';

import React, {useEffect, useState} from "react";
import {useRouter, useParams} from "next/navigation";
import styles from './ProblemDetail.module.css';
import {getToken, getUserNickname} from '../../../../token';  // ÏµúÏã† ÏΩîÎìúÏóêÏÑú Ï†úÍ≥µÌïú Ìï®Ïàò ÏÇ¨Ïö©

const API_BASE_URL = process.env.NEXT_PUBLIC_API_BASE_URL || "https://ec2-3-34-134-27.ap-northeast-2.compute.amazonaws.com/api/wargame-problems";
const FILE_BASE_URL = API_BASE_URL.replace('/api/wargame-problems', '') || "https://ec2-3-34-134-27.ap-northeast-2.compute.amazonaws.com";

interface Problem {
  id: number;
  title: string;
  creator: string;
  level: string;
  detail: string;
  problemFile?: string;
  probelmFileSize?: number | null;
  kind: string;
  type: string;
  tags?: string[];
  reviewer?: string;
  source?: string;
  correctCount: number;
  entireCount: number;
  createdAt: string;
  updatedAt: string;
}

interface Ranking {
  nickname: string;
  firstBlood: string;
  id: number;
}

interface Comment {
  id: number;
  creator: { nickname: string };
  createdAt: string;
  content: string;
  isEditing?: boolean;
}

interface ApiResponse<T> {
  result: T;
}

const CTFProblemPage = () => {
  const {id: problemIdParam} = useParams();
  const router = useRouter();
  const problemId = problemIdParam || "6";

  const [problem, setProblem] = useState<Problem | null>(null);
  const [flag, setFlag] = useState<string>("");
  const [message, setMessage] = useState<string>("");
  const [comments, setComments] = useState<Comment[]>([]);
  const [newComment, setNewComment] = useState<string>("");
  const [ranking, setRanking] = useState<Ranking[]>([]);
  const [vmAddress, setVmAddress] = useState<string>("");
  const [userNickname, setUserNickname] = useState<string | null>(null);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchToken = async () => {
      const token = await getToken();
      if (!token) {
        alert("Î°úÍ∑∏Ïù∏Ïù¥ ÌïÑÏöîÌïú ÏÑúÎπÑÏä§ÏûÖÎãàÎã§.");
        router.push("/login");
      }
    };

    fetchToken();
  }, [router]);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const token = await getToken();
        if (!token) throw new Error("Token is not available");

        const problemRes = await fetch(`${API_BASE_URL}/${problemId}`, {
          headers: {'Authorization': `Bearer ${token}`},
        });
        const problemData: ApiResponse<Problem> = await problemRes.json();
        setProblem(problemData.result);

        const commentsRes = await fetch(`${FILE_BASE_URL}/api/comments/problem/${problemId}`, {
          headers: {'Authorization': `Bearer ${token}`},
        });
        const commentsData: ApiResponse<Comment[]> = await commentsRes.json();
        setComments(commentsData.result);

        const rankingRes = await fetch(`${FILE_BASE_URL}/api/problems/${problemId}/firstblood?size=5`, {
          headers: {'Authorization': `Bearer ${token}`},
        });
        const rankingData: ApiResponse<Ranking[]> = await rankingRes.json();
        setRanking(rankingData.result);

        const nickname = await getUserNickname();
        setUserNickname(nickname);
      } catch (error) {
        console.error("Îç∞Ïù¥ÌÑ∞ Í∞ÄÏ†∏Ïò§Í∏∞ Ïã§Ìå®:", error);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, [problemId]);

  const handleSubmit = async () => {
    const token = await getToken();
    if (!token) return;

    try {
      const res = await fetch(`${FILE_BASE_URL}/api/problems/${problemId}/solve?flag=${encodeURIComponent(flag)}`, {
        method: "GET",
        headers: {"Authorization": `Bearer ${token}`},
      });

      if (!res.ok) {
        setMessage("Ï†ïÎãµ ÌôïÏù∏ Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.");
        return;
      }

      const data = await res.json();
      setMessage(data.result ? "Ï†ïÎãµÏûÖÎãàÎã§! Ï∂ïÌïòÌï©ÎãàÎã§." : "Ïò§ÎãµÏûÖÎãàÎã§. Îã§Ïãú ÏãúÎèÑÌïòÏÑ∏Ïöî.");
    } catch (error) {
      console.error("Ï†ïÎãµ ÌôïÏù∏ Ïã§Ìå®:", error);
      setMessage("Ï†ïÎãµ ÌôïÏù∏ Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.");
    }
  };

  const handleShowVmAddress = async () => {
  const token = await getToken();
  if (!token) {
    alert("Î°úÍ∑∏Ïù∏ ÌõÑ Ïù¥Ïö©Ìï† Ïàò ÏûàÏäµÎãàÎã§.");
    return;
  }

  try {
    const response = await fetch(
      `${FILE_BASE_URL}/api/pods/create?userId=${userNickname}&problemId=${problemId}`,
      {
        method: "POST",
        headers: {
          "Authorization": `Bearer ${token}`,
        },
      }
    );

    if (!response.ok) {
      throw new Error("VM Ï£ºÏÜå ÏÉùÏÑ±Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.");
    }

    const data = await response.json();
    setVmAddress(data.result || "VM Ï£ºÏÜåÎ•º ÏÉùÏÑ±Ìï† Ïàò ÏóÜÏäµÎãàÎã§.");
  } catch (error) {
    console.error("VM Ï£ºÏÜå ÏÉùÏÑ± Ïã§Ìå®:", error);
    setVmAddress("VM Ï£ºÏÜå ÏÉùÏÑ± Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.");
  }
};

  const handleFileDownload = async () => {
  const token = await getToken();
  if (!token) {
    alert("Î°úÍ∑∏Ïù∏Ïù¥ ÌïÑÏöîÌïú ÏÑúÎπÑÏä§ÏûÖÎãàÎã§.");
    return;
  }

  try {
    const response = await fetch(
      `${FILE_BASE_URL}/api/problems/${problem?.id}/download`,
      {
        method: "GET",
        headers: {
          "Authorization": `Bearer ${token}`,
        },
      }
    );

    if (!response.ok) {
      const errorText = await response.text();
      console.error("ÌååÏùº Îã§Ïö¥Î°úÎìú Ïã§Ìå®:", errorText);
      alert(`ÌååÏùº Îã§Ïö¥Î°úÎìú Ïã§Ìå®: ${response.status} ${response.statusText}`);
      return;
    }

    const blob = await response.blob();
    const url = window.URL.createObjectURL(blob);
    const a = document.createElement("a");
    a.href = url;
    a.download = problem?.problemFile || "downloaded_file";
    document.body.appendChild(a);
    a.click();
    a.remove();
    window.URL.revokeObjectURL(url);
  } catch (error) {
    console.error("ÌååÏùº Îã§Ïö¥Î°úÎìú Ï§ë Î¨∏Ï†úÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§:", error);
    alert("ÌååÏùº Îã§Ïö¥Î°úÎìú Ï§ë Î¨∏Ï†úÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§. Îã§Ïãú ÏãúÎèÑÌï¥Ï£ºÏÑ∏Ïöî.");
  }
};

  const handleCommentSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!newComment.trim()) return;

    const token = await getToken();
    if (!token) {
      alert("Î°úÍ∑∏Ïù∏ ÌõÑ ÎåìÍ∏ÄÏùÑ ÏûëÏÑ±Ìï† Ïàò ÏûàÏäµÎãàÎã§.");
      return;
    }

    try {
      const res = await fetch(`${FILE_BASE_URL}/api/comments`, {
        method: "POST",
        headers: {
          "Authorization": `Bearer ${token}`,
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          type: "PROBLEM",
          problemId,
          contents: newComment,
        }),
      });

      if (!res.ok) throw new Error("ÎåìÍ∏Ä Îì±Î°ù Ïã§Ìå®");
      const data = await res.json();
      setComments([data.result, ...comments]);
      setNewComment("");
    } catch (error) {
      console.error("ÎåìÍ∏Ä Îì±Î°ù Ïã§Ìå®:", error);
    }
  };

  const handleCommentDelete = async (commentId: number) => {
    const token = await getToken();
    if (!token) {
      setError('Î°úÍ∑∏Ïù∏Ïù¥ ÌïÑÏöîÌï©ÎãàÎã§.');
      return;
    }

    try {
      const response = await fetch(
          `${FILE_BASE_URL}/api/comments/${commentId}`,
          {method: 'DELETE', headers: {Authorization: `Bearer ${token}`}}
      );

      if (!response.ok) throw new Error('ÎåìÍ∏Ä ÏÇ≠Ï†úÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.');
      setComments((prev) => prev.filter((comment) => comment.id !== commentId));
    } catch (error) {
      setError(error instanceof Error ? error.message : 'ÎåìÍ∏Ä ÏÇ≠Ï†ú Ï§ë Î¨∏Ï†úÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.');
    }
  };

  const handleCommentEditToggle = (commentId: number) => {
    setComments((prev) =>
        prev.map((comment) =>
            comment.id === commentId ? {...comment, isEditing: !comment.isEditing} : comment
        )
    );
  };

  const handleCommentEdit = async (commentId: number, newContent: string) => {
    const token = await getToken();
    if (!token) {
      setError('Î°úÍ∑∏Ïù∏Ïù¥ ÌïÑÏöîÌï©ÎãàÎã§.');
      return;
    }

    try {
      const response = await fetch(
          `${FILE_BASE_URL}/api/comments/${commentId}`,
          {
            method: 'PUT',
            headers: {
              'Content-Type': 'application/json',
              Authorization: `Bearer ${token}`,
            },
            body: JSON.stringify({
              type: 'PROBLEM',
              contents: newContent,
            }),
          }
      );

      if (response.status === 403) {
        throw new Error('Í∂åÌïúÏù¥ ÏóÜÏäµÎãàÎã§.');
      }
      if (!response.ok) {
        throw new Error('ÎåìÍ∏Ä ÏàòÏ†ïÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.');
      }

      const updatedComment = await response.json();

      setComments((prev) =>
          prev.map((comment) =>
              comment.id === commentId ? {...comment, content: updatedComment.contents, isEditing: false} : comment
          )
      );
    } catch (error) {
      setError(error instanceof Error ? error.message : 'ÎåìÍ∏Ä ÏàòÏ†ï Ï§ë Î¨∏Ï†úÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.');
    }
  };

  if (loading) return <p>Î¨∏Ï†úÎ•º Î∂àÎü¨Ïò§Îäî Ï§ëÏûÖÎãàÎã§...</p>;

  if (!problem) return <p>Î¨∏Ï†úÎ•º Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§.</p>;

  return (
  <section className={styles.container}>
    <div className={styles.mainContent}>
      <h3 className={styles.title}>{problem.title}</h3>
      <p className={styles.metaInfo}>
        Ï∂úÏ†úÏûê: {problem.creator} | Ï¢ÖÎ•ò: {problem.kind}
      </p>
      <p className={styles.metaInfo}>
        Ï∂úÏ†úÏùº: {new Date(problem.createdAt).toLocaleDateString()} | Î¶¨Î∑∞Ïñ¥: {problem.reviewer}
      </p>
      <p className={`${styles.metaInfo} ${styles.last}`}>
        Ï†ïÎãµÎ•†: {problem.entireCount === 0 ? 'Ï†úÏ∂ú ÏóÜÏùå' : `${((problem.correctCount / problem.entireCount) * 100).toFixed(2)}%`}
        ({problem.correctCount}/{problem.entireCount})
      </p>

      {problem.tags?.length > 0 && (
        <div className={styles.metaInfo}>
          ÌÉúÍ∑∏: {problem.tags.join(', ')}
        </div>
      )}

      {problem.source && (
        <div className={styles.metaInfo}>
          Ï∂úÏ≤ò: {problem.source}
        </div>
      )}

      {/* Î¨∏Ï†ú ÏÑ§Î™Ö ÏÑπÏÖò */}
      <div className={styles.viewerContainer}>
        <div dangerouslySetInnerHTML={{ __html: problem.detail }} />
      </div>

      {/* Ï†ïÎãµ Ï†úÏ∂ú ÏÑπÏÖò */}
      <div className={styles.flagSection}>
        <h4 className={styles.flagTitle}>Ï†ïÎãµ Ï†úÏ∂ú</h4>
        <form className={styles.flagForm} onSubmit={handleSubmit}>
          <input
            type="text"
            className={styles.flagInput}
            value={flag}
            onChange={(e) => setFlag(e.target.value)}
            placeholder="Ï†ïÎãµÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî"
          />
          {message && <p className={styles.flagMessage}>{message}</p>}
          <button className={styles.flagButton} type="submit">Ï†úÏ∂ú</button>
        </form>
      </div>

      <div className={styles.buttonContainer}>
        <div className={styles.buttonBox}>
          <button
            className={styles.downloadButton}
            onClick={handleFileDownload}
            disabled={!problem?.problemFile}
          >
            ÌååÏùº Îã§Ïö¥Î°úÎìú
          </button>
        </div>
        <div className={styles.buttonBox}>
          <button onClick={handleShowVmAddress} className={styles.vmButton}>
            VM Ï£ºÏÜå Î≥¥Í∏∞
          </button>
          {vmAddress && <p className={styles.vmAddress}>{vmAddress}</p>}
        </div>
      </div>
            {/* Îû≠ÌÇπ Î∞ïÏä§ ÏÑπÏÖò */}
      {ranking && ranking.length > 0 ? (
  <div className={styles.rankingBox}>
    <h4 className={styles.rankingTitle}>üèÜ Îû≠ÌÇπ</h4>
    <ul className={styles.rankingList}>
      {ranking.map((rank, index) => (
        <li key={rank.id} className={styles.rankingItem}>
          <span className={styles.rankNumber}>{index + 1}</span>
          <span className={styles.rankName}>{rank.nickname}</span>
          <span className={styles.rankTime}>{rank.firstBlood}</span>
        </li>
      ))}
    </ul>
  </div>
) : (
  <div className={styles.rankingBox}>
    <h4 className={styles.rankingTitle}>üèÜ Îû≠ÌÇπ</h4>
    <p className={styles.noRanking}>ÏïÑÏßÅ Î¨∏Ï†úÎ•º Ìëº ÏÇ¨ÎûåÏù¥ ÏóÜÏäµÎãàÎã§.</p>
  </div>
)}

      <div className={styles.commentsSection}>
        <h4 className={styles.commentTitle}>ÎåìÍ∏Ä</h4>
        <form onSubmit={handleCommentSubmit} className={styles.formGroup}>
          <textarea
            value={newComment}
            onChange={(e) => setNewComment(e.target.value)}
            placeholder="ÎåìÍ∏ÄÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî"
          />
          <button type="submit" disabled={isSubmitting} className={styles.btnPrimary}>
            {isSubmitting ? 'Îì±Î°ù Ï§ë...' : 'Îì±Î°ù'}
          </button>
        </form>
        <ul>
          {comments.map((c) => (
            <li key={c.id} className={styles.commentItem}>
              <p>
                <strong>{c.creator?.nickname || 'ÏùµÎ™Ö ÏÇ¨Ïö©Ïûê'}</strong>
              </p>
              {c.isEditing ? (
                <textarea
                  value={c.content}
                  onChange={(e) =>
                    setComments((prev) =>
                      prev.map((comment) =>
                        comment.id === c.id
                          ? { ...comment, content: e.target.value }
                          : comment
                      )
                    )
                  }
                  placeholder="ÎåìÍ∏ÄÏùÑ ÏàòÏ†ïÌïòÏÑ∏Ïöî..."
                />
              ) : (
                <p className={styles.commentContent}>{c.content}</p>
              )}
              <span className={styles.commentMeta}>
                | {new Date(c.createdAt).toLocaleDateString()}
              </span>
              {c.creator?.nickname === userNickname && (
                <>
                  <span
                    onClick={() => handleCommentEditToggle(c.id)}
                    className={styles.commentEdit}
                  >
                    {c.isEditing ? 'Ï†ÄÏû•' : 'ÏàòÏ†ï'}
                  </span>
                  <span
                    onClick={() => handleCommentDelete(c.id)}
                    className={styles.commentDelete}
                  >
                    ÏÇ≠Ï†ú
                  </span>
                </>
              )}
            </li>
          ))}
        </ul>
      </div>
    </div>
  </section>
);
}

export default CTFProblemPage;